
import { ApiRequest, API_URLS } from '@/api/request.api';
import { defineComponent, ref } from 'vue';
import dayjs from 'dayjs';
import RtLoading from '@/components/RtLoading.vue';

export default defineComponent({
  components: { RtLoading },
  name: 'Lager',
  setup() {
    const loading = ref(true);
    const items = ref([]);
    new ApiRequest(API_URLS.SEARCH_ITEM)
      .get()
      .execute()
      .then((req) => {
        items.value = req.data;
      })
      .catch((err) => {
        throw new Error(err);
      })
      .finally(() => {
        loading.value = false;
      });
    return { loading, items, dayjs };
  },
});
