<template>
  <rt-loading class="w-28 mx-auto" :loaderSize="8" v-if="loading"></rt-loading>
  <table v-else class="table-auto text-rttext-500 bg-gray-50 w-full">
    <thead class="bg-rttext-500 text-white text-left">
      <th class="border-b border-white p-2 hidden md:table-cell">
        Produkt-Code
      </th>
      <th class="border-b border-white p-2">Produktname</th>
      <th class="border-b border-white p-2">Anzahl/Menge</th>
      <th class="border-b border-white p-2 hidden md:table-cell">MHD</th>
    </thead>
    <tbody>
      <tr
        :class="{
          'bg-red-300': item.is_gram ? item.amount < 1000 : item.amount < 3,
        }"
        class="hover:bg-rttext-500 hover:text-white"
        v-for="item in items"
        :key="item.id"
      >
        <td class="border-b border-green-600 p-2 hidden md:table-cell">
          {{ item.product_code }}
        </td>
        <td class="border-b border-green-600 p-2">{{ item.product_name }}</td>
        <td class="border-b border-green-600 p-2 text-right">
          {{ item.is_gram ? item.amount + 'g' : item.amount + '' }}
        </td>
        <td
          class="border-b border-green-600 p-2 text-center hidden md:table-cell"
        >
          {{
            item.good_till === '1992-10-30'
              ? '-'
              : dayjs(item.good_till).format('DD.MM.YYYY')
          }}
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script lang="ts">
import { ApiRequest, API_URLS } from '@/api/request.api';
import { defineComponent, ref } from 'vue';
import dayjs from 'dayjs';
import RtLoading from '@/components/RtLoading.vue';

export default defineComponent({
  components: { RtLoading },
  name: 'Lager',
  setup() {
    const loading = ref(true);
    const items = ref([]);
    new ApiRequest(API_URLS.SEARCH_ITEM)
      .get()
      .execute()
      .then((req) => {
        items.value = req.data;
      })
      .catch((err) => {
        throw new Error(err);
      })
      .finally(() => {
        loading.value = false;
      });
    return { loading, items, dayjs };
  },
});
</script>
